.button {
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.1);
}

button {
  border-style: dotted;
}

.wp-block-site-logo {
  max-width: 36px;
}

.has-background-background-color {
  top: 0;
  position: fixed;
  width: 100vw;
  left: 0;
  right: 0;
  z-index: 100;
  padding: 1.4rem;
}

.wp-block-navigation svg {
  width: 2rem;
  height: auto;
}

.wp-block-image {
  border-radius: 8px;
  overflow: hidden;
}

.wp-block-buttons {
  width: 100%;
}
.wp-block-buttons .wp-block-button {
  width: 100%;
}
@media screen and (min-width: 600px) {
  .wp-block-buttons .wp-block-button {
    width: auto;
  }
}
.wp-block-buttons .wp-block-button .wp-element-button {
  width: 100%;
}
.wp-block-buttons .wp-block-button .wp-element-button .wp-block-button__width-50 {
  width: 100%;
}
@media screen and (min-width: 600px) {
  .wp-block-buttons .wp-block-button .wp-element-button {
    width: auto;
  }
}

.wp-block-button__link {
  transition: all ease-in-out 0.3s;
}
.wp-block-button__link:hover {
  transform: scale(1.02);
}